.connect{

    &-block{
        padding-top: 102px;
        padding-bottom: 166px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 950px;
        @media(max-width:501px){
            flex-direction: column-reverse;
        }
        &__left{
            max-width: 540px;
            background: #FFFFFF;
            box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
            border-radius: 8px;
            padding: 55px 48px;
            @media(max-width:540px){
                padding: 40px 30px;
            }
            // @media(max-width:501px){
            //     padding: 55px 48px;
            // }
            &--input{

                &-name, &-email, &-subject, &-textarea{
                    margin-bottom: 30px;
                    &>input{
                    outline: none;
                    
                    padding: 25px;
                    width: 443px;
                    height: 60px;
                    background: #FFFFFF;
                    border: 1px solid #E3E7F6;
                    border-radius: 6px;
                        @media(max-width:860px){
                            width: 350px;
                        }
                        @media(max-width:860px){
                            width: 300px;
                        }
                        @media(max-width:640px){
                            width: 200px;
                        }
                        @media(max-width:540px){
                            width: 170px;
                            height: 40px;
                        }
                        // @media(max-width:501px){
                        //     width: 443px;
                        //     height: 60px;
                        // }
                    }
                    &>p{
                        line-height: 150%;
                        letter-spacing: 0.005em;
                        color: #8F90A6;
                        @media(max-width:540px){
                            font-size: 14px;
                        }
                        // @media(max-width:501px){
                        //     font-size: 16px;
                        // }
                    }
                }
                &-textarea{
                    &>textarea{
                        width: 443px;
                        height: 148px;
                        background: #FFFFFF;
                        border: 1px solid #E3E7F6;
                        border-radius: 6px;
                        @media(max-width:860px){
                            width: 350px;
                        }
                        @media(max-width:860px){
                            width: 300px;
                        }
                        @media(max-width:640px){
                            width: 200px;
                            height: 100px;
                        }
                        @media(max-width:540px){
                            width: 170px;
                        }
                        // @media(max-width:501px){
                        //     width: 443px;
                        //     height: 148px;
                        // }
                        }
                }
            }
        }
        &__right{
            margin-left: 10px;
            &--country{
                margin-top: 72px;
                @media(max-width:501px){
                    margin-bottom: 72px;
                    margin-top: 0;
                }
                &-flex{
                    display: flex;
                    align-items: center;
                    margin-bottom: 25px;
                    &>p{
                        margin-left: 20px;
                        font-weight: 700;
                        font-size: 18px;
                        line-height: 150%;
                        letter-spacing: 0.005em;
                        color: #000000;
                        @media(max-width:640px){
                            font-size: 12px;
                        }
                        // @media(max-width:501px){
                        //     font-size: 18px;
                        // }
                    }
                }
                &>p{
                    max-width: 273px;
                    @media(max-width:800px){
                        width: 200px;
                    }
                    @media(max-width:640px){
                        font-size: 12px;
                    }
                    // @media(max-width:501px){
                    //     font-size: 18px;
                    // }
                }
            }
        }
    }
}