.button{
    display: flex;
    align-items: center;

    @media(max-width:950px){
        flex-direction: column;
        align-items: flex-start;
    }
    &__red{
        margin-right: 16px;
        @media(max-width:950px){
            margin-bottom: 25px;
        }
        &--a{
            padding: 14px 32px;
            font-weight: 400;
            font-size: 16px;
            line-height: 33px;
            color: white;
            background: #BF0830;
            border-radius: 6px;
            transition: all 0.5s ease;
            border: 1px solid #BF0830;
            @media(max-width:700px){
                padding: 10px 20px;
            }
            &:hover{
                background: transparent;
                color: #BF0830;
                
            }
        }
    }
    &__white{

        &--a{
            padding: 14px 32px;
            font-weight: 400;
            font-size: 16px;
            line-height: 33px;
            color: #BF0830;
            background: transparent;
            border: 1px solid #BF0830;
            border-radius: 6px;
            transition: all 0.5s ease;
            @media(max-width:700px){
                padding: 10px 20px;
            }
            &:hover{
                background: #BF0830;
                color: white;
            }
        }
    }
}