.partner{
    overflow: hidden;
    &__carts{
        margin-bottom: 83px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        @media(max-width:500px){
            flex-direction: column;   
        }
        &-cart{
            margin: 10px;
            background: #FFFFFF;
            box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
            border-radius: 8px;
            &>h3{
                text-align: center;
                padding-top: 53px;
                margin-bottom: 18px;
                @media(max-width:999px){
                    font-size: 50px;
                    padding-top: 30px;
                    margin-bottom: 14px;
                }
                @media(max-width:700px){
                    font-size: 30px;
                    padding-top: 20px;
                    margin-bottom: 0px;
                }
                @media(max-width:500px){   
                    font-size: 40px;
                    padding-top: 30px;
                    margin-bottom: 10px;
                }
            }
            &>p{
                text-align: center;
                margin-bottom: 12px;
                @media(max-width:999px){
                    font-size: 18px;
                    line-height: 23px;
                }
                @media(max-width:700px){
                    font-size: 10px;
                    line-height: 10px;
                }
                @media(max-width:500px){   
                    font-size: 16px;
                    line-height: 15px;
                }
                
            }
            &>img{
                margin: 0 auto;
                padding: 0 5px;
                @media(max-width:999px){
                    width: 80%;
                    max-width: 80%;
                    height: auto;
                }
            }
        }
    }
}
.rocket-about{
    padding: 0 13px;
    transition: all 1s ease;
    transform: translate(-20%, 0);
    opacity: 0;
}
.employees-about{
    padding-top: 5px;
    transition: all 1s ease;
    transform: translate(0, 20%);
    opacity: 0;
}
.users-about{
    padding: 0 5px;
    padding-top: 10px;
    transition: all 1s ease;
    transform: translate(20%, 0);
    opacity: 0;
}