.products{
    &-cookies{
        margin-top: 102px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media(max-width:800px){
            flex-direction: column-reverse;
        }
        &__left{
            margin-right: 10px;
            transform: translateX(-10%);
            transition: all 1s ease;
            opacity: 0;
            max-width: 419px;   
            @media(max-width:800px){
                max-width: 80%;
            }
            & > img{
                margin-bottom: 40px;

            }
            & > p{
                margin-bottom: 20px;
            }

        }
        &__right{
            transform: translateX(10%);
            transition: all 1s ease;
            opacity: 0;
            @media(max-width:800px){
                margin-bottom: 20px;
            }
        }
    }
    &-webtoffee{
        margin-top: 150px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 82px;
        @media(max-width:800px){
            flex-direction: column;
        }
        &__left{
            transform: translateX(-10%);
            transition: all 1s ease;
            opacity: 0;
            @media(max-width:800px){
                margin-bottom: 20px;
            }
        }
        &__right{
            margin-left: 10px;
            transform: translateX(10%);
            transition: all 1s ease;
            opacity: 0;
            max-width: 470px;
            @media(max-width:800px){
                max-width: 80%;
            }
            & > img{
                margin-bottom: 30px;

            }
            & > p{
                margin-bottom: 20px;
            }
        }
    }
}