.wedo{
    &__what{
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding-top: 145px;
        padding-bottom: 125px;
        @media(max-width:1000px){
            flex-direction: column;
        }
        &-left{

            transition: all 1s ease;
            opacity: 0;
            transform: translate(-20%, 0);
        }
        &-right{
            transition: all 1s ease;
            opacity: 0;
            transform: translate(20%, 0);
            max-width: 397px;
            &>h3{
                margin-bottom: 16px;
            }
        }
    }
}