
.burger-menu_button {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 30;
  width: 80px;
  height: 80px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.burger-menu_button:hover .burger-menu_lines {
  filter: brightness(0.7);
}

.burger-menu_lines::before,
.burger-menu_lines::after,
.burger-menu_lines {
  position: absolute;
  width: 40px;
  height: 3px;
  background-color: #BB1E99;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.burger-menu_lines {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.burger-menu_lines::before {
  content: '';
  top: -12px;
}
.burger-menu_lines::after {
  content: '';
  top: 12px;
}


.burger-menu_active .burger-menu_lines {
  background-color: transparent;
}
.burger-menu_active .burger-menu_lines::before {
  top: 0;
  transform: rotate(45deg);
}
.burger-menu_active .burger-menu_lines::after{
  top: 0;
  transform: rotate(-45deg);
}

.burger-menu_nav {
  padding-top: 120px;
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 20;
  display: flex;
  flex-flow: column;
  align-items: center;
  height: 100%;
  
  background: linear-gradient(252.91deg, #edeef0 3.68%, rgba(227, 231, 246, 0) 97.82%);
  -webkit-transition: 0.8s;
  -moz-transition: 0.8s;
  -o-transition: 0.8s;
  transition: all 0.8s ease;
  & > li{
    list-style: none;
    margin-bottom: 20px;
  }
}
.active {
  top:150%

}
.navigation__link-burger {
  padding: 18px 35px;
  font-size: 18px;
  text-decoration: none;
  letter-spacing: 5px;
  font-weight: 400;
  color: #464855;
  position: relative;
  transform: translate(0, 30px);
  transition: all 1s ease 0.5s;
  line-height: 33px;
    &:hover{           
      &::before{
        transform: scalex(1);
      }
    }
    &::before{
      content: "";
      position: absolute;
      left: 0;
      bottom: -7px;
      height: 2px;
      width: 80%;
      border-radius: 10px;
      background: #BF0830;
      transform: scalex(0);
      transition: all 0.5s ease;
      transform-origin: left;
  }
}
.burger-menu_link:first-child {
  border-top: 1px solid #fff;
}
.burger-menu_link:hover {
  filter: brightness(0.9);
}
.burger-menu_overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
}
.burger-menu_active .burger-menu_overlay {
  display: block;
  background-color: rgba(0, 0, 0, 0.5);
}
.burger-menu{
    display: none;
}
@media(max-width: 600px){
    .burger-menu {
      display: block;
    }
  }