.culture{
    background: linear-gradient(268.52deg, #E3E7F6 -52.27%, rgba(227, 231, 246, 0) 138.23%);
    &__text{
        &-title{
            padding-top: 65px;
            margin-bottom: 16px;
            text-align: center;
        }
        &-subtitle{
            text-align: center;
            margin-bottom: 97px;
        }
    }
    &__grid{
        display: grid;
        grid-template-columns:345px 345px 345px;
        column-gap: 36px;
        row-gap: 36px;
        margin: 0 auto;
        grid-template-areas:
                            "0 1 2"
                            "3 4 5";
        @media(max-width:1300px){
            grid-template-columns:1fr 1fr;
            gap: 17px;
            grid-template-areas:
                                "0 1"
                                "2 3"
                                "4 5";
        }
        @media(max-width:700px){
            grid-template-columns:1fr;
            gap: 0;
            row-gap: 17px;
            grid-template-areas:
                                "0"
                                "1"
                                "2" 
                                "3"
                                "4" 
                                "5";
        }
        &-element{
            background: #FFFFFF;
            box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
            border-radius: 8px;
            height: 100%;
            transition: all 1s ease;
            &:hover{
                height: 90%;
            }
            &--block{
                height: 100%;
                padding: 40px;
                @media(max-width:700px){
                    padding: 20px;
                }
                &-img{
                    margin-bottom: 33px;
                    display: flex;
                    align-items: center;
                    &>h4{
                        margin: auto 0;
                        margin-left: 15px;
                        font-size: 28px;
                        line-height: 31px;
                        color: #3A3A3C;
                    }
                }
                &>h4{
                    font-size: 28px;
                    line-height: 31px;
                    color: #3A3A3C;
                    margin-bottom: 32px;
                }
                & > p{
                    @media(max-width:700px){
                        line-height: 17px;
                    }
                }
            }
        }
    }
    &__who{
        margin-top: 97px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding-bottom: 70px;
        @media(max-width:1000px){
            flex-direction: column-reverse;
        }
        &-left{
            transition: all 1s ease;
            opacity: 0;
            transform: translate(-20%, 0);
            max-width: 397px;
            &>h3{
                margin-bottom: 16px;
            }

        }
        &-right{
            transition: all 1s ease;
            opacity: 0;
            transform: translate(20%, 0);
        }
    }
}
.grid-block-0{
    grid-area: 0;
}
.anim-block-0{
    transition: all 1s ease;
    opacity: 0;
    transform: translate(-20%, -20%);
}
.grid-block-1{
    grid-area: 1;
}
.anim-block-1{
    transition: all 1s ease;
    opacity: 0;
    transform: translate(0, -20%);
}
.grid-block-2{
    grid-area: 2;
}
.anim-block-2{
    transition: all 1s ease;
    opacity: 0;
    transform: translate(20%, -20%);
}
.grid-block-3{
    grid-area: 3;
}
.anim-block-3{
    transition: all 1s ease;
    opacity: 0;
    transform: translate(-20%, 20%);
}
.grid-block-4{
    grid-area: 4;
}
.anim-block-4{
    transition: all 1s ease;
    opacity: 0;
    transform: translate(0, 20%);
}
.grid-block-5{
    grid-area: 5;
}
.anim-block-5{
    transition: all 1s ease;
    opacity: 0;
    transform: translate(20%, 20%);
}