.inovation{
    margin-top: 70px;
    &__title{
        & > h3{
            text-align: center;
            font-weight: 700;
            margin-top: 120px;
            margin-bottom: 12px;
        }
        & > p{
            text-align: center;
            margin-bottom: 156px;
        }
    }
}
.swiper{
}
.swiper-wrapper{
    display: flex;
    align-items: center;
    padding:45px 0;
    
}
.swiper-slide{
        background: #FFFFFF;
        box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
        border-radius: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 119px;
        & > img{
            height: 119px;
        }
}