html,
body {
	height: 100%;
	font-family: 'PT Sans';
	background: white;
}
.wrap{
	margin: 0 auto;
	max-width: 1080px;
	width: 100%;
}
.wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100%;
}
.main {
	flex: 1 0 auto;
}
.footer {
	flex: 0 0 auto;
}

.container{
	padding: 0 10px;
	max-width: 1170px;
  	margin: 0 auto;
}
.animate{
	transform: translate(0, 0);
	opacity: 1;
}
span{
	color: #BF0830;
}
h3{
	font-weight: 700;
	font-size: 44px;
	line-height: 62px;
	color: #1A2578;
}
p{
	font-weight: 400;
	font-size: 16px;
	line-height: 27px;
	color: #484C63;
}