.header-home{
    overflow: hidden;
    position: relative;
    background: linear-gradient(252.91deg, #E3E7F6 3.68%, rgba(227, 231, 246, 0) 97.82%);
    @media(max-width:950px){
        padding-bottom: 50px ;
    }
    &__navigation{
        padding: 24px 0 97px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &--logo{
            opacity: 0;
            transform: translate(0, -10%);
            transition: all 1s ease;
            & > img{
                
            }
        }
        &--nav{
            display: flex;
            align-items: center;
            @media(max-width:600px){
            display: none;
    }
        }
    }
    &__main{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media(max-width: 699px){
            flex-direction: column-reverse;
        }
        &-left{
            &--text{
                opacity: 0;
                transform: translate(20px, 0);
                transition: all 1s ease 0.5s;
                &>h1{
                    font-weight: 700;
                    font-size: 48px;
                    line-height: 62px;
                    color: #1A2578;
                    margin-bottom: 32px;
                    max-width: 570px;
                    @media(max-width: 699px){
                        margin-top: 25px;
                    }
                }
                &>p{
                    margin-bottom: 40px;
                    max-width: 512px;
                }
            }
        }
        &-right{
            opacity: 0;
            transform: translate(0, 20%);
            transition: all 1s ease;
            img{
                width: 100%;
                max-width: 100%;
                height: auto;
                max-width: 847px;
                
            }
        }
    }
}
.navigation{
    display: flex;
    align-items: center;
    & > li{
        list-style: none;
        margin-right: 60px;
        &:last-child{
            margin-right: 30px;
        }
        @media(max-width: 699px){
            margin-right: 30px;
            &:last-child{
                margin-right: 15px;
            }
        }
    }
    &__link{
        font-weight: 400;
        font-size: 16px;
        line-height: 33px;
        color: #464855;
        position: relative;
        transform: translate(0, 30px);
        transition: all 1s ease 0.5s;
        opacity: 0;

        &:hover{
                
            &::before{
                transform: scalex(1);
            }
        }
        &::before{
            content: "";
            position: absolute;
            left: 0;
            bottom: -7px;
            height: 2px;
            width: 80%;
            border-radius: 10px;
            background: #BF0830;
            transform: scalex(0);
            transition: all 0.5s ease;
            transform-origin: left;
        }
    }
    &__hiring{
    }
}