.future{
    background: linear-gradient(342.12deg, #E3E7F6 -6.53%, rgba(227, 231, 246, 0) 87.81%);
    &__button{
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;

        @media(max-width:950px){
            flex-direction: column;
            align-items: flex-start;
    }
    }
    &__text{
        padding-bottom: 137px;
        & > h3{
            padding-top: 136px;
            text-align: center;
            margin-bottom: 20px;
        }
        & > p{  
            text-align: center;
            margin-bottom: 40px;
        }
    }
}