.team{
    padding-top: 110px;
    padding-bottom: 110px;
    background: linear-gradient(335.73deg, #E3E7F6 12.46%, rgba(227, 231, 246, 0) 84.19%);
    &__block{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @media(max-width:400px){
            flex-direction: column-reverse;

        }
        &-left{
            max-width: 343px;
            & > h3{
                margin-bottom: 16px;
            }
            & > p{
                margin-bottom: 16px;
            }
        }
        &-right{

        }
    }
}