.product{
    &__text{
        &--title{
            & > h3{
                margin-top: 106px;
                text-align: center;
                margin-bottom: 16px;
                
            }
        }
        &--subtitle{
            & > p{
                text-align: center;
                margin-bottom: 97px;
            }
        }
    }
    &__carts{
        padding-bottom: 108px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        overflow: hidden;
        @media(max-width: 688px){
            padding-top: 70px;
            flex-direction: column;
        }
        &--cart{
            @media(max-width: 688px){
                margin-bottom: 15px;
            }
            background: linear-gradient(0deg, #FFFFFF, #FFFFFF), #FFFFFF;
            box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.08);
            opacity: 0;
            transform: translate(-10%, 0);
            transition: all 1s ease;
            &:hover{
                transform: translateY(-5px);
            }
            &-block{
                max-width: 375px;
                @media(max-width: 900px){
                    max-width: 250px
                }
                margin:auto;
                padding-top: 64px;
                & > img{
                    margin-bottom: 40px;
                }
                & > p{
                    margin-bottom: 40px;
                }
            }
            background: #FFFFFF;
            box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.08);
            border-radius: 8px;
            transition: all 0.5s ease ;
            & > img{
                width: 100%;
                max-width: 100%;
                height: auto;
                padding: 0 6px 0 64px;
            }

        }
    }
}
.button-flex{
    display: flex;
    max-width: 160px;
    align-items: center;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: #BF0830;
    & > img{
        width: 14px;
        margin-right: 9px;
        height: 14px;
    }
}
.block-top{
    padding-top: 75px;
    @media(max-width: 900px){
        padding-top: 70px;
    }
    @media(max-width: 700px){
        padding-top: 65px;
    }
}
.second{
    opacity: 0;
    transform: translate(10%, 0);
    transition: all 1s ease;
}