.reccomend{
    padding-top: 56px;

    &__block{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 900px;
        margin: auto;
        padding: 50px 0;
        @media(max-width:800px){
            flex-direction: column;
        }
        &-left{
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            & > h3{
                margin-left: 27px;
                @media(max-width:800px){
                    margin-left: 0;
                    text-align: center;
                }
            }
            @media(max-width:800px){
                margin-bottom: 30px;
            }
        }
        &-right{
            &>img{
                margin: 0 auto;
            }
            &--stars{
                
                &>h3{
                    color: #000000;
                }
                display: flex;
                align-items: center;
            }
            &--mask{
                justify-content: center;
                align-items: center;
                display: flex;
                cursor: pointer; 
                &>p{
                    transition: all 0.5s ease;
                    color: #424B5A;
                    margin-right: 4px;
                    &:hover{
                        text-decoration-line: underline;
                    }
                }
            }
        }
    }
    &__join{
        &-text{
            text-align: center;
            &>p{
                margin-top: 20px;
                margin-bottom: 100px;
            }
        }
        &-developer{
            &--block{
                transform: translateY(30%);
                opacity: 0;
                transition: all 1s ease;
                background: #FFFFFF;
                box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 17px 35px rgba(22, 23, 72, 0.05);
                border-radius: 8px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 35px;
                margin-bottom: 30px;

                text-align: center;
                @media(max-width:610px){
                    flex-direction: column;
                }
                &:last-child{
                    margin-bottom: 68px;
                }
                &-left{
                    @media(max-width:610px){
                        margin-bottom: 20px;
                    }
                    &>h3{
                        font-weight: 400;
                        font-size: 28px;
                        line-height: 27px;
                        color: #484C63;
                        margin-bottom: 24px;
                        
                    }
                    &-block{
                        display: flex;
                        align-items: center;
                        @media(max-width:610px){
                            justify-content: center;
                        }
                        &__time{
                            display: flex;
                            align-items: center;
                        }
                        &__india{
                            display: flex;
                            align-items: center;
                            margin-left: 30px;
                        }   
                    }
                }
                &-right{

                }
            }
        }
        &-see{
            display: flex;
            justify-content: center;
            margin-bottom: 44px;
        }
    }   
}
.reccomend-big__block{
    background: #F7F8FF;
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    display: flex;
    align-items: center;
    margin-bottom: 127px;
}
