.footer-home{
    background: #0D123C;
    &__logo{
        
        @media(max-width:580px){
            margin: 0 auto;

        }
        &>p{
            margin-top: 30px;
            max-width: 287px;
            color: #FFFFFF;
            @media(max-width:900px){
                max-width: 150px;
            }
            @media(max-width:580px){
                margin-bottom: 30px;
                max-width: 100%;
            }
        }
    }
    &__right{
        display: flex;
        justify-content: space-between;
        @media(max-width:400px){
            flex-direction: column;
        }
        
        &--products, &--about, &--follows{
            & > p{
                margin-bottom: 30px;
                color: #FFFFFF;
                transition: all 0.3s ease;
                cursor: pointer;
                @media(max-width:700px){
                    margin-bottom: 20px;
                    &:hover{
                        text-decoration-line: underline;
                    }
                
                }
            }

            & > ul{
                padding: 0;
                margin: 0;
                @media(max-width: 400px){
                    margin-bottom: 20px;
                    transition: all 1s ease;
                    transform: translate(0, 0);
                }
                & > li{
                    position: relative;
                    @media(max-width: 400px){
                        &:last-child{
                            &::after{
                                content: "";
                                position: absolute;
                                bottom: -7px;
                                left: 0;
                                width: 100%;
                                height: 2px;
                                background: white;
                            }
                        }
                    }
                    margin-bottom: 21px;
                    & > a{
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 20px;
                        color: #FFFFFF;
                        transition: all 0.3s ease;
                        &:hover{
                            text-decoration-line: underline;
                        }
                    }
                    
                    list-style: none;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
            }
        }
        &--about{
            margin-right: 126px;
            @media(max-width:900px){
                margin-right: 90px;
            }
            @media(max-width:700px){
                margin-right: 60px;
            }
            @media(max-width:500px){
                margin-right: 40px;
            }
        }
        &--products{
            margin-right: 103px;
            @media(max-width:900px){
                margin-right: 80px;
            }
            @media(max-width:700px){
                margin-right: 60px;
            }
            @media(max-width:500px){
                margin-right: 40px;
            }
        }
        &--follows{
            &-img{
                display: flex;
                & > a{
                    
                    margin-right: 15px;
                    &:last-child{
                        margin-right: 0;
                    }
                }
            }
        }
    }
    &__hr{
        width: 100%;
        background: rgba(197, 203, 226, 0.5);
        border: 1px solid rgba(197, 203, 226, 0.5);
        margin-bottom: 29px;
    }
    &__bottom{
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        padding-bottom: 38px;
        & > p{
            & > a{
                margin: 5px;
                font-weight: 400;
                font-size: 16px;
                line-height: 20px;
                color: #FFFFFF;
                transition: all 0.3s ease;
                &:hover{
                    text-decoration-line: underline;
                }
            }
            
        }
    }
}
.footer-block{
    padding-top: 57px;
    padding-bottom: 64px;
    display: flex;
    justify-content: space-between;
    @media(max-width:580px){
        flex-direction: column;
    }
}
.hidden{
    transform: translateY(10%);
    opacity: 1;
    display: none;
}