.creators{
    background: linear-gradient(268.52deg, #E3E7F6 -52.27%, rgba(227, 231, 246, 0) 138.23%);
    padding-bottom: 20px;
    padding-top: 73px;
    &-block{
        display: flex;
        justify-content: space-between;
        align-items: center;
        &__left{
            max-width: 416px;
            & > h3{
                margin-bottom: 16px;
                @media(max-width:600px){
                    font-size: 35px;
                    line-height: 37px;
                }
            }
            & > p{
                @media(max-width:600px){
                    font-size: 12px;
                    line-height: 17px;
                }
            }
        }
    }
}