.life{
    padding-top: 50px;
    background: linear-gradient(270.76deg, #E3E7F6 -55.5%, rgba(227, 231, 246, 0) 145.36%);
    &__flex{
        
        &-string{
            display: flex;
            justify-content: space-between;
            @media(max-width:600px){
                flex-direction: column-reverse;
            }
            &--left{
                transform: translateX(-30%);
                opacity: 0;
                transition: all 1s ease;
                margin-right: 15px;
                @media(max-width:600px){
                    margin-right: 0;
                    margin-bottom: 10px;
                }
                &>h3{
                    margin-bottom: 16px;
                }
                &>p{
                max-width: 323px;
                }
                &>img{
                    border-radius: 10px;
                }
            }
            &--right{
                transform: translateX(30%);
                opacity: 0;
                transition: all 1s ease;
                margin-left: 15px;
                @media(max-width:600px){
                    margin-left: 0;
                    margin-bottom: 10px;
                }
                &>img{
                    border-radius: 10px;
                }
            }
        }
    }
}
.flex-string__center{
    align-items: center;
    
}
.life__flex-bottom{
    margin-bottom: 50px;
}
.flex-string__end{
    align-items: flex-end;
    padding-bottom: 50px;
}