.partner{
    &__title{
        &> h2{
            margin-top: 101px;
            margin-bottom: 79px;
            font-weight: 700;
            font-size: 36px;
            line-height: 57px;
            text-align: center;
            color: #1A2578;
            & > span{
                color: #BF0830;
                position: relative;
                &:hover{
                
                    &::before{
                        transform: scalex(1);
                    }
                }
                &::before{
                    content: "";
                    position: absolute;
                    left: 0;
                    bottom: -7px;
                    height: 2px;
                    width: 100%;
                    border-radius: 10px;
                    background: #BF0830;
                    transform: scalex(0);
                    transition: all 0.5s ease;
                    transform-origin: left;
                }
            }
        }
    }
    &__brands{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        align-items: center;
        padding-bottom: 127px;
        @media(max-width: 900px){
            justify-content: center;
        }
        &-anim{
            opacity: 0;
            transform: translate(10px, 0);
            transition: all 1s ease 0.2s;
            &:nth-child(1){
                transition: all 1s ease 0.2s;
            }
            &:nth-child(2){
                transition: all 1s ease 0.3s;
            }
            &:nth-child(3){
                transition: all 1s ease 0.4s;
            }
            &:nth-child(4){
                transition: all 1s ease 0.5s;
            }
            &:nth-child(5){
                transition: all 1s ease 0.6s;
            }
            &:nth-child(6){
                transition: all 1s ease 0.7s;
            }
            &:nth-child(7){
                transition: all 1s ease 0.8s;
            }
            & > img{
                transition: all 0.5s ease;
                padding: 0 10px;
                
                @media(max-width: 1000px){
                    margin-bottom: 15px;
                }
                &:hover{
                    opacity: 0.4;
                }
            }    
        }
    }
}